.cards{
    display: flex;
    width: 100%;
    flex-direction: column;
}

.card-intro{
    display: flex;
    padding: 30px;
    padding-top: 150px;
    width: 100%;
    background: linear-gradient(to bottom, rgb(218, 243, 232), rgb(252, 237, 237));
}

.card-intro-text{
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: 100px;
}

.card-intro-text h1{
    font-size: 3.5rem;
    width: 80%;
}

.card-intro-text p{
    margin-top: 50px;
    width: 70%;
    font-size: 1.3rem;
    line-height: 1.5;
}

.card-btn{
    margin-top: 30px;
    padding: 25px;
    outline: none;
    cursor: pointer;
    border: none;
    border-radius: 7px;
    color: #fff;
    font-weight: 600;
    background: #000;
    font-size: 1.2rem;
    width: 60%;
}

.card-intro-img{
    display: flex;
    width: 50%;
    justify-content: center;
    align-items: center;
}

.card-intro-img img{
    max-width: 100%;
}

/* responsive */

@media screen and (max-width: 991px){
    .cards{
        display: flex;
        width: 100%;
        flex-direction: column;
    }
    
    .card-intro{
        flex-direction: column;
        padding: 20px;
        padding-top: 30px;
    }
    
    .card-intro-text{
        margin-top: 30px;
        width: 100%;
        margin-left: 0px;
    }
    
    .card-intro-text h1{
        font-size: 2rem;
        width: 100%;
    }
    
    .card-intro-text p{
        width: 100%;
        font-size: 1rem;
    }
    
    .card-btn{
        width: 60%;
    }
    
    .card-intro-img{
        display: flex;
        width: 100%;
        margin-top: 30px;
    }
    
    .card-intro-img img{
        max-width: 100%;
    }
}

/* end card intro */



.standard{
    display: flex;
    padding: 150px 30px;
    width: 100%;
    justify-content: space-between;
}

.business{
    display: flex;
    padding: 150px 30px;
    width: 100%;
    justify-content: space-between;
    background: rgb(252, 242, 242);
}

.standard-img{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}

.standard-img img{
    max-width: 100%;
}

.business-img{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}

.business-img img{
    max-width: 100%;
}

.standard-text{
    display: flex;
    flex-direction: column;
    width: 50%;
}

.standard-text h1{
    font-size: 3rem;
    font-weight: 700;
}

.standard-text p{
    margin-top: 30px;
    font-size: 1.3rem;
    width: 85%;
    line-height: 1.3;
}

.options{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
}

.options p{
    color: blue;
    display: flex;
    align-items: center;
    font-weight: 700;
}

.options p i{
    margin-right: 30px;
    display: flex;
}


/* responsiveness */

@media screen and (max-width: 991px){
    .standard{
        flex-direction: column;
    }

    .business{
        display: flex;
        padding: 30px;
        width: 100%;
        justify-content: space-between;
        flex-direction: column;
        background: rgb(252, 242, 242);
    }

    .business-img{
        width: 100%;
        margin-top: 100px;
    }
    
    .business-img img{
        max-width: 100%;
    }
    
    
    .standard-img{
        width: 100%;
    }
    
    .standard-img img{
        max-width: 100%;
    }
    
    .standard-text{
        width: 100%;
        margin-top: 30px;
    }
    
    .standard-text h1{
        font-size: 2rem;
        font-weight: 700;
    }
    
    .standard-text p{
        margin-top: 20px;
        font-size: 1rem;
    }
    
    .options{
        display: flex;
        flex-direction: column;
        margin-top: 30px;
    }
    
    .options p{
        color: blue;
        display: flex;
        align-items: center;
        font-weight: 700;
    }
    
    .options p i{
        margin-right: 30px;
        display: flex;
    }   
    
    .standard-text button{
        width: 60%;
        padding: 20px;
        font-size: 1rem;
    }
}
/* end standard styles */